import React, { useState } from "react";
import classes from "./good.module.css";
import { Link } from "react-router-dom";
import { ROUTES } from "const/Constants";
import { useStores } from "stores/root.store";
import { observer } from "mobx-react";

export const Product = observer(({ product }) => {
    const { favoriteStore } = useStores();

    const toggleFavorite = (e) => {
        e.preventDefault();
        favoriteStore.toggle(product);
    };

    return (
        <Link to={`${ROUTES.PRODUCTS}/${product.id}`}>
            <div className={classes.productCard}>
                <div className={classes.img}>
                    {product.sale > 0 ? <div className={classes.sale}>-{product.sale}%</div> : null}
                    <div
                        className={favoriteStore.exist(product) ? classes.favourites_active : classes.favourites}
                        onClick={(e) => toggleFavorite(e)}></div>
                    <img src={product.photo} alt={product.name} />
                </div>
                <div className={classes.info}>
                    <div className={classes.info__header}>
                        <h4 className={classes.name}>{product.name}</h4>
                        <div className={classes.brand}>{product.model}</div>
                        <div className={classes.oldPrice}>
                            {product.sale > 0 ? Math.ceil(product.price * ((100 - product.sale) / 100)) : null}
                        </div>
                    </div>
                    <div className={classes.info__footer}>
                        <div className={classes.price}>{product.price} </div>
                        <div className={classes.btn}>
                            <img src="./source/img/icon/rightArrow.svg" alt="rightArrow" />
                        </div>
                    </div>
                </div>
            </div>
        </Link>
    );
});
