import { makeAutoObservable } from "mobx";
import { makePersistable } from "mobx-persist-store";

export class FavoriteStore {
    favorite;
    rootStore;

    constructor(rootStore) {
        makeAutoObservable(this);
        makePersistable(this, { name: "favorite", properties: ["favorite"], storage: window.localStorage });
        this.favorite = [];
        this.rootStore = rootStore;
    }

    addProduct(product) {
        this.favorite.push(product);
    }

    toggle(product) {
        this.exist(product) ? this.removeProduct(product) : this.addProduct(product);
    }

    removeProduct(product) {
        this.favorite = this.favorite.filter((item) => item.id !== product.id);
    }

    exist(product) {
        return this.favorite.find((item) => item.id == product.id);
    }

    get count() {
        return this.favorite.length;
    }
}
