import { Link } from "react-router-dom";
import classes from "./Accordion.module.css";
import { useState } from "react";

export const Accordion = ({ title, children, path }) => {
    let [open, setOpen] = useState(false);
    const toggle = () => {
        setOpen(!open);
    };
    return (
        <div className={classes.accordion}>
            <div className={classes.header + " " + (open ? classes.open : "")} onClick={toggle}>
                <div className={classes.title}>{title} </div>
                <div className={classes.arrow}>
                    {open ? (
                        <img src="./source/img/icon/miniArrowUp.svg" alt="miniArrowUp" />
                    ) : (
                        <img src="./source/img/icon/miniArrowDown.svg" alt="miniArrowDown" />
                    )}
                </div>
            </div>
            {open && (
                <div className={classes.subCat}>
                    {children.map((child, i) => (
                        <Link to={path + child.url} key={i}>
                            {child.name}
                        </Link>
                    ))}
                </div>
            )}
        </div>
    );
};
