import classes from "./AddBtn.module.css";
import { observer } from "mobx-react";

export const AddBtn = observer(({ decrement, increment, count }) => {
    return (
        <div className={classes.addBtn + " addBtn"}>
            {count > 0 ? (
                <div className={classes.controlWrap}>
                    <div className={classes.control} onClick={decrement}>
                        -
                    </div>
                    <div className={classes.count}>{count}</div>
                    <div className={classes.control} onClick={increment}>
                        +
                    </div>
                </div>
            ) : (
                <div className={classes.mainBtn + " mainBtn"} onClick={increment}>
                      
                </div>
            )}
        </div>
    );
});
