import { useParams } from "react-router-dom";
import { AddBtn } from "components/UI/addBtn";
import { BreadCrumps } from "components/UI/breadCrumps";
import { Page404 } from "pages/404/";
import classes from "./product.module.css";
import GoodsSlider from "components/UI/goodsSlider/goodsSlider";
import { useStores } from "stores/root.store";
import { useEffect, useRef, useState } from "react";
import { observer } from "mobx-react";
import { ROUTES } from "const/Constants";
import { StockScale } from "components/UI/stockScale";
import { Radio } from "components/UI/radio";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

export const Products = observer(() => {
    const { basketStore, productsStore, favoriteStore, categoryStore } = useStores();
    const params = useParams();
    const product = productsStore.getProduct(+params.id);

    if (!product) return <Page404 />;
    const inBasket = basketStore.availability(+params.id);
    const category = categoryStore.getCategory({ id: product.category[0] });
    const slider = useRef(null);
    const [description, setDescription] = useState("specifications");
    const favorite = favoriteStore.exist(product);

    const sliderSettings = {
        dots: true,
        infinite: false,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: false,
        appendDots: (dots) => (
            <div>
                <ul className={classes.dotsNavigation}> {dots} </ul>
            </div>
        ),
        customPaging: (i) => <div></div>,
    };

    const decrement = () => {
        basketStore.removeProduct(product);
    };
    const increment = () => {
        basketStore.addProduct(product);
    };
    const toggleFavorite = () => {
        favoriteStore.toggle(product);
    };
    const colorHandler = (color) => {
        console.log(color);
    };
    const infoHandler = (value) => {
        setDescription(value);
    };
    const photoHandler = (src) => {
        console.log(src);
    };

    useEffect(() => {
        slider.current.slickGoTo(0);
    }, [product]);

    return (
        <div className="container">
            <BreadCrumps
                pathArr={[
                    // { url: "/catalog", name: "" },
                    { url: ROUTES.CATALOG + category.url, name: category.name },
                ]}
            />
            <div className={classes.product}>
                <div className={classes.photo}>
                    <div className={classes.mainPhoto}>
                        <img src={product.photo} alt={product.name} />
                    </div>
                    <div className={classes.thumbnailBar}>
                        <Radio onChange={photoHandler} key={product.id}>
                            <div data-value={product.photo} className={classes.thumbnail + " active"}>
                                <div className={classes.item}>
                                    <img src={product.photo} alt={product.name} />
                                </div>
                            </div>
                            <div data-value={product.photo} className={classes.thumbnail}>
                                <div className={classes.item}>
                                    <img src={product.photo} alt={product.name} />
                                </div>
                            </div>
                            <div data-value={product.photo} className={classes.thumbnail}>
                                <div className={classes.item}>
                                    <img src={product.photo} alt={product.name} />
                                </div>
                            </div>
                            <div data-value={product.photo} className={classes.thumbnail}>
                                <div className={classes.item}>
                                    <img src={product.photo} alt={product.name} />
                                </div>
                            </div>
                            <div data-value={product.photo} className={classes.thumbnail}>
                                <div className={classes.item}>
                                    <img src={product.photo} alt={product.name} />
                                </div>
                            </div>
                            <div data-value={product.photo} className={classes.thumbnail}>
                                <div className={classes.item}>
                                    <img src={product.photo} alt={product.name} />
                                </div>
                            </div>
                            <div data-value={product.photo} className={classes.thumbnail}>
                                <div className={classes.item}>
                                    <img src={product.photo} alt={product.name} />
                                </div>
                            </div>
                        </Radio>
                    </div>
                </div>

                <div className={classes.detail}>
                    <div className="product__article">. KRSB12IP</div>
                    <h2>      () iPhone 12 (RED) A2172 US (, AASP)</h2>
                    <div className={classes.modelWrap}>
                        <div className={classes.model}>
                            <h3></h3>
                            <div className={classes.radioModel}>
                                <Radio onChange={colorHandler} key={product.id}>
                                    <div data-value="iPhone 12" className={"active"}>
                                        iPhone 12
                                    </div>
                                    <div data-value="iPhone 12 pro">iPhone 12 pro </div>
                                </Radio>
                            </div>
                        </div>
                        <div className={classes.color}>
                            <h3></h3>
                            <div className={classes.radioColor}>
                                <Radio onChange={colorHandler} key={product.id}>
                                    <div data-value="red" className={classes.red + " active"}></div>
                                    <div data-value="green" className={classes.green}></div>
                                    <div data-value="blue" className={classes.blue}></div>
                                    <div data-value="white" className={classes.white}></div>
                                    <div data-value="black" className={classes.black}></div>
                                    <div data-value="purple" className={classes.purple}></div>
                                </Radio>
                            </div>
                        </div>
                    </div>
                    <div className={classes.priceWrap}>
                        <div className={`${classes.price} h2`}>{product.price}</div>
                        {product.sale > 0 ? (
                            <>
                                <div className={classes.sale}>-{product.sale}%</div>
                                <div className={classes.oldPrice}>
                                    {Math.ceil(product.price * ((100 - product.sale) / 100))}
                                </div>
                            </>
                        ) : null}
                    </div>
                    <div className={classes.mainBtnWrap}>
                        <AddBtn count={inBasket} increment={increment} decrement={decrement} />
                        <div className={classes.favorite} onClick={toggleFavorite}>
                            {favorite ? (
                                <img src="./source/img/icon/favouritesActive.svg" alt="" />
                            ) : (
                                <img src="./source/img/icon/favourites.svg" alt="" />
                            )}
                        </div>
                    </div>
                    <div className={classes.stockWrap}>
                        <StockScale stock={product.inStock} />
                    </div>
                </div>
            </div>

            <div className={classes.productMobile}>
                <div className={classes.photo}>
                    <Slider ref={slider} {...sliderSettings}>
                        <img src={product.photo} alt={product.name} />
                        <img src={product.photo} alt={product.name} />
                        <img src={product.photo} alt={product.name} />
                        <img src={product.photo} alt={product.name} />
                        <img src={product.photo} alt={product.name} />
                        <img src={product.photo} alt={product.name} />
                    </Slider>
                </div>

                <div className={classes.article}>. KRSB12IP</div>
                <h2>      () iPhone 12 (RED) A2172 US (, AASP)</h2>

                <div className={classes.priceSection}>
                    <div className={classes.priceWrap}>
                        <div className={classes.price}>{product.price}</div>

                        {product.sale > 0 ? (
                            <>
                                <div className={classes.sale}>-{product.sale}%</div>
                                <div className={classes.oldPrice}>
                                    {Math.ceil(product.price * ((100 - product.sale) / 100))}
                                </div>
                            </>
                        ) : null}
                    </div>
                    <div className={classes.favorite} onClick={toggleFavorite}>
                        {favorite ? (
                            <img src="./source/img/icon/favouritesActive.svg" alt="" />
                        ) : (
                            <img src="./source/img/icon/favourites.svg" alt="" />
                        )}
                    </div>
                </div>

                <div className={classes.modelWrap}>
                    <div className={classes.model}>
                        <h3></h3>
                        <div className={classes.radioModel}>
                            <Radio onChange={colorHandler}>
                                <div data-value="iPhone 12" className={"active"}>
                                    iPhone 12
                                </div>
                                <div data-value="iPhone 12 pro">iPhone 12 pro </div>
                            </Radio>
                        </div>
                    </div>
                    <div className={classes.color}>
                        <h3></h3>
                        <div className={classes.radioColor}>
                            <Radio onChange={colorHandler} key={product.id}>
                                <div data-value="red" className={classes.red + " active"}></div>
                                <div data-value="green" className={classes.green}></div>
                                <div data-value="blue" className={classes.blue}></div>
                                <div data-value="white" className={classes.white}></div>
                                <div data-value="black" className={classes.black}></div>
                                <div data-value="purple" className={classes.purple}></div>
                            </Radio>
                        </div>
                    </div>
                </div>
            </div>

            <div className={classes.info}>
                <div>
                    <Radio onChange={infoHandler} key={product.id}>
                        <h3 data-value="specifications" className="active">
                            
                        </h3>
                        <h3 data-value="warranty"></h3>
                    </Radio>
                </div>
                {description === "specifications" && (
                    <div className={classes.body}>
                         , ,       
                            -   .  ,
                        , ,    ,    ,
                           .     
                             .
                    </div>
                )}
                {description === "warranty" && (
                    <div className={classes.body}>
                           ,    ,  
                         .       
                           .
                    </div>
                )}
            </div>

            <div className={classes.mobileBtnSection}>
                <div className={classes.stockWrap}>
                    <StockScale stock={product.inStock} />
                </div>
                <div className={classes.mainBtnWrap}>
                    <AddBtn count={inBasket} increment={increment} decrement={decrement} />
                </div>
            </div>

            <div className="sliderTitle">
                <h2>   </h2>
                <span> </span>
            </div>
            <GoodsSlider products={productsStore.products.slice(0, 5)} />
        </div>
    );
});
