import { Link, useLocation, useParams, useNavigate } from "react-router-dom";
import classes from "./FeedbackItem.module.css";
import { useStores } from "stores/root.store";
import { ROUTES } from "const/Constants";
import { observer } from "mobx-react";
import { Pagination } from "components/UI/pagination";
import { useState, useEffect, useRef } from "react";

export const FeedbackHeader = ({ avatar, username }) => {
    return (
        <div className={classes.feedbackHeader}>
            <div className={classes.avatar}>
                <img src={avatar} alt="avatar" />
            </div>
            <div className={classes.username}>{username}</div>
        </div>
    );
};

export const FeedbackItem = ({ feedback }) => {
    const { userStore } = useStores();
    const user = userStore.getUser(feedback.userId);
    const date = new Date(feedback.date).toLocaleString("ru", {
        day: "numeric",
        month: "numeric",
        year: "numeric",
    });
    const commentRef = useRef(null);
    const [open, setOpen] = useState(false);
    const [showComment, setShowComment] = useState(false);

    useEffect(() => {
        setShowComment(commentRef.current.scrollHeight > commentRef.current.offsetHeight);
    }, []);

    const toggleComment = () => {
        setOpen(!open);
    };

    return (
        <div className={classes.feedback} key={feedback.id}>
            <FeedbackHeader avatar={user.avatar} username={feedback.username} />

            <div className={classes.body + " " + (open && classes.open)} ref={commentRef}>
                {feedback.text}
            </div>
            <div className={classes.footer}>
                {showComment && (
                    <div className={classes.openBtn} onClick={toggleComment}>
                        {open ? <></> : <> </>}
                    </div>
                )}
                <div>{date}</div>
            </div>
        </div>
    );
};
