export class ProductsStore {
    products;
    rootStore;

    constructor(rootStore) {
        this.products = this.createCategory();
        this.rootStore = rootStore;
        makeAutoObservable(this);
    }
    getProduct(id: number) {
        return this.products.find((item) => item.id == id);
    }

    random(min: number, max: number) {
        return Math.floor(Math.random() * (max - min + 1)) + min;
    }

    createCategory() {
        return Array.from(new Array(10000)).map((item, i) => {
            const product = JSON.parse(JSON.stringify(initProducts[this.random(0, initProducts.length - 1)]));
            product.id = +i + 1;

            if (product.photoRange)
                product.photo = "./source/photo/" + this.random(product.photoRange[0], product.photoRange[1]) + ".jpg";
            product.inStock = this.random(0, 25);
            product.price = this.random(1000, 13000);
            product.sale = initSale[this.random(0, 10)];
            return product;
        });
    }

    getProducts(category) {
        console.log("getProducts");
        return this.products.filter((item) => ~item.category.indexOf(category));
    }
}
