export class CategoryStore {
    category;
    rootStore;

    constructor(rootStore) {
        makeAutoObservable(this);
        this.category = initCategory;
        this.rootStore = rootStore;
    }

    findCategory(arr, prop, value) {
        for (let i = 0; i < arr.length; i++) {
            if (arr[i][prop] == value) return arr[i];
            if (arr[i].children) {
                const find = this.findCategory(arr[i].children, prop, value);
                if (find) return find;
            }
        }
        return false;
    }

    getCategory(obj) {
        const [[prop, value]] = Object.entries(obj);
        return this.findCategory(this.category, prop, value);
    }
}
