import { BreadCrumps } from "components/UI/breadCrumps";
import classes from "./Feedback.module.css";
import { useStores } from "stores/root.store";
import { observer } from "mobx-react";
import { Pagination } from "components/UI/pagination";
import { FeedbackItem, FeedbackHeader } from "components/UI/feedbackItem";
import { Popup } from "components/popup";
import { useState } from "react";
import { useForm } from "react-hook-form";

export const Feedback = observer(() => {
    const { feedbackStore, userStore } = useStores();
    const [page, setPage] = useState(1);
    const {
        register,
        handleSubmit,
        reset,
        formState: { errors },
    } = useForm();
    const [activePopup, setActivePopup] = useState(false);
    const commentPerPage = 3;
    const pages = Math.ceil(feedbackStore.feedback.length / commentPerPage);
    const onsubmit = (data) => {
        const currentUser = userStore.currentUser;
        feedbackStore.addFeedback({
            userId: currentUser.id,
            username: data.nickname,
            text: data.feedback,
            date: +new Date(),
        });
        reset({ name: currentUser.username, feedback: "" });
        togglePopup();
    };

    const paginationHandler = (p) => {
        setPage(p);
    };
    const togglePopup = () => {
        setActivePopup(!activePopup);
    };

    return (
        <div className="container">
            <Popup active={activePopup} togglePopup={togglePopup}>
                <div className={classes.addFeedbackPopup}>
                    <FeedbackHeader avatar={userStore.currentUser.avatar} username={userStore.currentUser.username} />
                    <form onSubmit={handleSubmit(onsubmit)}>
                        <input
                            {...register("nickname", {
                                value: userStore.currentUser.username,
                                required: true,
                                minLength: 2,
                                maxLength: 50,
                            })}
                            className={classes.addCommentInput + " " + (errors.nickname && "error")}
                            placeholder=""
                        />
                        <textarea
                            {...register("feedback", {
                                required: true,
                                minLength: 2,
                                maxLength: 500,
                            })}
                            className={classes.addCommentText + " " + (errors.feedback && "error")}
                            placeholder=" "></textarea>

                        <button className="mainBtn" type="submit">
                             
                        </button>
                    </form>
                </div>
            </Popup>
            <div className={classes.breadCrumps}>
                <BreadCrumps pathArr={[{ name: "" }]} />
            </div>

            {feedbackStore.feedback.length < 1 ? (
                <h2 className={classes.empty}> </h2>
            ) : (
                <>
                    <h2 className={classes.feedbackH2}></h2>
                    <div className={classes.feedbackSection}>
                        <div className={classes.feedbackWrap}>
                            {feedbackStore.getFeedback(page, 3).map((feedback) => (
                                <FeedbackItem feedback={feedback} key={feedback.id} />
                            ))}
                        </div>
                    </div>

                    <div className={classes.controlSection}>
                        <div className={classes.button}>
                            <div className="mainBtn" onClick={togglePopup}>
                                 
                            </div>
                        </div>
                        <div className={classes.paginationBlock}>
                            <Pagination props={{ active: page, length: pages, onClick: paginationHandler }} />
                        </div>
                    </div>
                </>
            )}
        </div>
    );
});
