import { BreadCrumps } from "components/UI/breadCrumps";
import { Link, useLocation, useParams, useNavigate } from "react-router-dom";
import classes from "./Favorite.module.css";
import { useStores } from "stores/root.store";
import { ROUTES } from "const/Constants";
import { observer } from "mobx-react";
import { Product } from "components/UI/good/good";

export const Favorite = observer(() => {
    const { favoriteStore } = useStores();

    return (
        <div className="container">
            <BreadCrumps pathArr={[{ name: "" }]} />
            <div className={classes.favorite}>
                {favoriteStore.favorite.length < 1 ? (
                    <h2 className={classes.empty}>  </h2>
                ) : (
                    <div className={classes.favoriteWrap}>
                        {favoriteStore.favorite.map((product) => (
                            <>
                                <Product product={product} key={+product.id} />
                            </>
                        ))}
                    </div>
                )}
            </div>
        </div>
    );
});
