import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { cloneElement, Children } from "react";

export const Radio = ({ children, onChange }) => {
    function handlerClick(e) {
        e.currentTarget.parentNode.querySelector(".active").classList.remove("active");
        e.currentTarget.classList.toggle("active");
        onChange(e.currentTarget.dataset.value);
    }
    return (
        <>
            {Children.map(children, (child) => {
                // const active = child.props["data-name"] === current ? " active" : "";
                return cloneElement(child, {
                    onClick: handlerClick,
                    // className: child.props.className + active,
                });
            })}
        </>
    );
};
