import { Link } from "react-router-dom";
import "./footer.css";
import { useStores } from "stores/root.store";
import { ROUTES } from "const/Constants";

export const Footer = () => {
    const { categoryStore } = useStores();
    return (
        <>
            <div className="footer">
                <div className="container">
                    <div className="footer__innerWrap">
                        <div className="footer__social">
                            <img src="./source/img/logo.svg" alt="Logo" />
                            <div className="footer__fullAddress">
                                350000,  , . , . .  , 1  1596325871,
                                 987456321
                            </div>
                            <img
                                className="socialMediaIcon"
                                src="./source/img/icon/socialMedia.svg"
                                alt="socialMedia"
                            />
                        </div>
                        <div className="footer__contacts">
                            <h4></h4>
                            <ul>
                                <li></li>
                                <li>info@dr.smartron.ru</li>
                                <li>8 800 800-80-08</li>
                                <li>8 (999) 999-99-99</li>
                                <li>,    , 1</li>
                            </ul>
                        </div>
                        <div className="footer__catalog">
                            <h4></h4>
                            <ul>
                                {categoryStore.category.map((cat) => (
                                    <li key={cat.id}>
                                        <Link to={ROUTES.CATALOG + cat.url}>{cat.name}</Link>
                                    </li>
                                ))}
                            </ul>
                        </div>
                        <div className="footer__menu">
                            <h4></h4>
                            <ul>
                                <li>
                                    <Link to={ROUTES.HOME}></Link>
                                </li>
                                <li>
                                    <Link to={ROUTES.WARRANTY}></Link>
                                </li>
                                <li> </li>
                                <li>-</li>
                                <li>
                                    <Link to={ROUTES.ABOUT}></Link>
                                </li>
                                <li>
                                    <Link to={ROUTES.BUYER}> </Link>
                                </li>
                                <li>
                                    <Link to={ROUTES.DELIVERY}>  </Link>
                                </li>
                                <li>
                                    <Link to={ROUTES.FEEDBACK}></Link>
                                </li>
                                <li>
                                    <Link to={ROUTES.CONTACTS}></Link>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
            <div>
                <div className="container">
                    <div className="privacyPolicy">
                        {" "}
                        <div> </div>
                        <div>2000 - 2022 |   </div>
                    </div>
                </div>
            </div>
            <div className="mobileNavBarPlug"></div>
        </>
    );
};
