import { makeAutoObservable } from "mobx";
import { makePersistable } from "mobx-persist-store";

export class BasketStore {
    basket;
    rootStore;

    constructor(rootStore) {
        makeAutoObservable(this);
        makePersistable(this, { name: "basket", properties: ["basket"], storage: window.localStorage });
        this.basket = [];
        this.rootStore = rootStore;
    }

    addProduct(product) {
        const find = this.basket.find((item) => item.id === product.id);
        if (!find) {
            this.basket.push({ id: product.id, quantity: 1 });
        } else ++find.quantity;
    }

    removeProduct(product) {
        this.basket.forEach((item, i) => {
            if (item.id === product.id) {
                item.quantity > 1 ? --item.quantity : this.basket.splice(i, 1);
            }
        });
    }

    get count() {
        return this.basket.reduce((acc, current) => current.quantity + acc, 0);
    }

    get totalAmount() {
        return this.basket.reduce(
            (acc, current) => current.quantity * this.rootStore.productsStore.getProduct(current.id).price + acc,
            0
        );
    }

    availability(productId) {
        const product = this.basket.find((item) => item.id === productId);
        return product ? product.quantity : 0;
    }

    deleteProduct(product) {
        this.basket.forEach((item, i) => item.id === product.id && this.basket.splice(i, 1));
    }
}
