import { makeAutoObservable } from "mobx";

const initUser = [
    {
        id: 1,
        username: " ",
        avatar: "./source/img/user/1.jpg",
        email: "",
    },
    {
        id: 2,
        username: " ",
        avatar: "./source/img/user/2.jpg",
        email: "",
    },
    {
        id: 3,
        username: " ",
        avatar: null,
        email: "",
    },
    {
        id: 4,
        username: " ",
        avatar: "./source/img/user/4.jpg",
        email: "",
    },
    {
        id: 5,
        username: " ",
        avatar: "./source/img/user/5.jpg",
        email: "",
    },
];

class userStore {
    users;
    _currentUser;
    notAuthorized = { username: " ", avatar: "./source/img/icon/user.svg" };
    rootStore;

    constructor(rootStore) {
        this.rootStore = rootStore;
        this.users = initUser;
        makeAutoObservable(this);
    }

    setUser = (user) => {
        Object.assign(this.users, user);
    };

    getUser(userId) {
        return this.users.find((user) => user.id == userId) || this.notAuthorized;
    }

    get currentUser() {
        return this.getUser(this._currentUser);
    }
}

export default userStore;
