import { Home } from "pages/home";
import { About } from "pages/about";
import { Products } from "pages/products";
import { Feedback } from "pages/feedback/Feedback";
import { Basket } from "pages/basket";
import { Delivery } from "pages/delivery";
import { Catalog } from "pages/catalog";
import { Buyer } from "pages/buyer";
import { Warranty } from "pages/warranty";
import { Contacts } from "pages/contacts";
import { Page404 } from "pages/404";
import { Favorite } from "pages/favorite";
import { ROUTES } from "const/Constants";
import "./style/reset.css";
import "./style/index.css";
import { Main } from "components/layouts/main";
import { useLocation, useRoutes } from "react-router-dom";
import { YMaps } from "react-yandex-maps";

const routes = {
    path: ROUTES.HOME,
    element: <Main />,
    children: [
        { index: true, element: <Home /> },
        { path: "/build", element: <Home /> },
        { path: ROUTES.ABOUT, element: <About /> },
        {
            path: ROUTES.PRODUCTS + "/:id",
            element: <Products />,
        },
        { path: ROUTES.BASKET, element: <Basket /> },
        { path: ROUTES.FAVORITE, element: <Favorite /> },
        { path: ROUTES.DELIVERY, element: <Delivery /> },
        { path: ROUTES.FEEDBACK, element: <Feedback /> },
        { path: ROUTES.CONTACTS, element: <Contacts /> },
        { path: ROUTES.CATALOG + ":category", element: <Catalog /> },
        { path: ROUTES.BUYER, element: <Buyer /> },
        { path: ROUTES.WARRANTY, element: <Warranty /> },
        { path: "*", element: <Page404 /> },
    ],
};

export const App = () => {
    const location = useLocation();
    console.log(location);
    const routing = useRoutes([routes]);
    return <YMaps>{routing}</YMaps>;
};
