import classes from "./home.module.css";
import GoodsSlider from "components/UI/goodsSlider/goodsSlider";
import { useStores } from "stores/root.store";
import { Link } from "react-router-dom";
import { ROUTES } from "const/Constants";

export const Home = () => {
    const { productsStore, categoryStore } = useStores();
    return (
        <div className="container">
            <div className={classes.banner}></div>
            <h2 className={`${classes.homeH2} ${classes.blackFont} h2MarginTop`}> </h2>
            <div className={classes.categoryCardWrap}>
                {categoryStore.category.map((cat) => (
                    <Link to={ROUTES.CATALOG + cat.url} key={cat.id}>
                        <div className={classes.categoryCard_item}>
                            <div className={classes.categoryCard_imgWrap}>
                                <img src={cat.photo} alt={cat.name} />
                            </div>
                            <span>{cat.name}</span>
                        </div>
                    </Link>
                ))}
            </div>

            <div className="sliderTitle">
                <h2> </h2>
                <span> </span>
            </div>
            <GoodsSlider products={productsStore.products.slice(10, 15)} />

            <div className="sliderTitle">
                <h2> </h2>
                <span> </span>
            </div>
            <GoodsSlider products={productsStore.products.slice(0, 5)} />
        </div>
    );
};
