import { makeAutoObservable } from "mobx";

const initFeedback = [
    {
        id: 1,
        userName: " ",
        userId: 1,
        text: " ,  ,               .  ,  ,           .",
        date: 1651183698803,
    },
    {
        id: 2,
        userId: 2,
        userName: " ",
        text: "         ...",
        date: 1651183658803,
    },
    {
        id: 3,
        userId: null,
        text: `    ,             .  ,                ,      .
       `,
        date: 1651163698803,
    },
    {
        id: 4,
        userId: 4,
        userName: " ",
        text: "    ,             .  ,                ,      .",
        date: 1651184698803,
    },
    {
        id: 5,
        userId: 5,
        userName: " ",
        text: "    ,             .  ,                ,      .",
        date: 1651143698803,
    },
];

export class FeedbackStore {
    feedback;
    rootStore;

    constructor(rootStore) {
        this.feedback = initFeedback;
        this.rootStore = rootStore;
        makeAutoObservable(this);
    }

    getFeedback(page: number, length: number) {
        const first = (page - 1) * length;
        return this.feedback.slice(first, first + length);
    }

    get lastId() {
        return this.feedback.reduce((acc, item) => {
            return item.id > acc ? item.id : acc;
        }, 0);
    }

    addFeedback(feedback) {
        feedback.id = this.lastId + 1;
        this.feedback.unshift(feedback);
    }
}
