import { BreadCrumps } from "components/UI/breadCrumps";
import { Link, useLocation, useParams, useNavigate } from "react-router-dom";
import classes from "./Basket.module.css";
import { useStores } from "stores/root.store";
import { ROUTES } from "const/Constants";
import { observer } from "mobx-react";

export const Basket = observer(() => {
    const { basketStore, productsStore } = useStores();

    const decrement = (product) => {
        basketStore.removeProduct(product);
    };
    const increment = (product) => {
        basketStore.addProduct(product);
    };
    const deleteProduct = (product) => {
        basketStore.deleteProduct(product);
    };

    return (
        <div className="container">
            <BreadCrumps pathArr={[{ name: "" }]} />
            <div className={classes.productWrap}>
                {basketStore.basket.length < 1 ? (
                    <h2 className={classes.empty}> </h2>
                ) : (
                    basketStore.basket.map((item) => {
                        const product = productsStore.getProduct(item.id);
                        return (
                            <div className={classes.product} key={product.id}>
                                <div className={classes.description}>
                                    <Link className={classes.img} to={`${ROUTES.PRODUCTS}/${product.id}`}>
                                        <img src={product.photo} alt={product.name} />
                                    </Link>
                                    <div className={classes.name}>{product.name}</div>
                                    <div className={classes.mobileDelete} onClick={() => deleteProduct(product)}>
                                        +
                                    </div>
                                </div>
                                <div className={classes.control}>
                                    <div className={classes.quantityControl}>
                                        {item.quantity > 1 ? (
                                            <div className={classes.quantityBtn} onClick={() => decrement(product)}>
                                                -
                                            </div>
                                        ) : (
                                            <div className={classes.quantityBtn + " " + classes.inactive}>-</div>
                                        )}

                                        <div className={classes.quantity}> {item.quantity}</div>
                                        <div className={classes.quantityBtn} onClick={() => increment(product)}>
                                            +
                                        </div>
                                    </div>
                                    <div className={classes.price}>{item.quantity * product.price}</div>
                                    <div className={classes.delete} onClick={() => deleteProduct(product)}>
                                        +
                                    </div>
                                </div>
                            </div>
                        );
                    })
                )}
            </div>
            <div className={classes.total}>
                  <span>{basketStore.totalAmount} </span>
            </div>
            <div className={classes.mainBtnWrap}>
                <div className="mainBtn"></div>
            </div>
        </div>
    );
});
