import React from "react";
import classes from "./goodsSlider.module.css";
import { Product } from "components/UI/good/good";

export default function goodSlider({ products }) {
    return (
        <div className={classes.goodsSlider}>
            {products.map((product) => (
                <Product product={product} key={product.id} />
            ))}
        </div>
    );
}
