import classes from "./Pagination.module.css";
import { useState, useRef, useEffect } from "react";

export const Pagination = ({ props }) => {
    const current = props.active;
    const [margin, setMargin] = useState(0);

    const sliderWrap = useRef(null);
    const slider = useRef(null);

    let sliderItem = [];
    let itemWidth = 48;
    for (let i = 1; i < props.length + 1; i++) {
        sliderItem.push(
            i == current ? (
                <div className={classes.active} key={i} style={{ width: itemWidth }}>
                    {i}
                </div>
            ) : (
                <div key={i} style={{ width: itemWidth }}>
                    {i}
                </div>
            )
        );
    }

    const leftMove = () => {
        const newMargin = margin + itemWidth * 4;
        setMargin(newMargin > 0 ? 0 : newMargin);
    };

    const rightMove = () => {
        const wrapWidth = Math.ceil(+getComputedStyle(sliderWrap.current).width.replace(/[^\.\-0-9]/g, ""));
        const limitMargin = -itemWidth * props.length + wrapWidth;
        const newMargin = margin - itemWidth * 4;
        setMargin(newMargin < limitMargin ? limitMargin : newMargin);
    };

    const onSlider = (e) => {
        if (Number.isInteger(+e.target.innerHTML)) {
            props.onClick(+e.target.innerHTML);
        }
    };

    useEffect(() => slideToCenter(), [current, props.length]);

    const slideToCenter = () => {
        const wrapWidth = Math.ceil(+getComputedStyle(sliderWrap.current).width.replace(/[^\.\-0-9]/g, ""));
        const sliderWidth = itemWidth * props.length;
        if (wrapWidth < itemWidth) {
            setMargin(0);
            return;
        }
        if (sliderWidth > wrapWidth) {
            var limitMargin = -(sliderWidth - wrapWidth);
            var newMargin = -(current * itemWidth - wrapWidth / 2) + itemWidth / 2;
            setMargin(newMargin > 0 ? 0 : newMargin < limitMargin ? limitMargin : newMargin);
        }
    };

    return (
        <div className={classes.pagination}>
            {props.length > 6 && (
                <div className={classes.control} onClick={leftMove}>
                    <svg width="8" height="12" viewBox="0 0 8 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path
                            d="M6.29294 0.292969L0.585938 5.99997L6.29294 11.707L7.70694 10.293L3.41394 5.99997L7.70694 1.70697L6.29294 0.292969Z"
                            fill="#CAD0DC"
                        />
                    </svg>
                </div>
            )}
            <div className={classes.sliderSection} onClick={onSlider} ref={sliderWrap}>
                <div className={classes.slider} style={{ marginLeft: margin }} ref={slider}>
                    {sliderItem}
                </div>
            </div>
            {props.length > 6 && (
                <div className={classes.control} onClick={rightMove}>
                    <svg width="8" height="12" viewBox="0 0 8 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path
                            d="M1.70697 11.707L7.41397 5.99997L1.70697 0.292969L0.292969 1.70697L4.58597 5.99997L0.292969 10.293L1.70697 11.707Z"
                            fill="#2E3563"
                        />
                    </svg>
                </div>
            )}
        </div>
    );
};
