import { Link, useLocation, useParams, useNavigate } from "react-router-dom";
import { AddBtn } from "components/UI/addBtn";
import { BreadCrumps } from "components/UI/breadCrumps";
import { Page404 } from "pages/404/";
import classes from "./StockScale.module.css";
import GoodsSlider from "components/UI/goodsSlider/goodsSlider";
import { useStores } from "stores/root.store";
import { useState } from "react";
import { observer } from "mobx-react";
import { ROUTES } from "const/Constants";

export const StockScale = observer(({ stock }) => {
    let description = "  ";
    let range = [
        { quantity: 0, description: " " },
        { quantity: 3, description: " " },
        { quantity: 6, description: " " },
        { quantity: 15, description: " " },
    ];

    return (
        <div className={classes.stockWrap}>
            <div className={classes.lineIndicator}>
                {range.map((item) => {
                    if (stock > item.quantity) description = item.description;
                    return <div className={stock > item.quantity ? classes.active : ""} key={item.quantity}></div>;
                })}
            </div>
            <div className={classes.description}>{description}</div>
        </div>
    );
});
