import { Link } from "react-router-dom";
import { useStores } from "stores/root.store";
import { useState } from "react";
import { observer } from "mobx-react";
import { CustomLink } from "components/UI/customLink";
import { Search } from "components/UI/search";
import { ROUTES } from "const/Constants";
import classes from "./Headers.module.css";
import { Accordion } from "components/UI/accordion";

export const Header = observer(() => {
    const { basketStore, favoriteStore, categoryStore } = useStores();
    let [menuOpen, setMenuOpen] = useState(false);
    let [catalogOpen, setCatalogOpen] = useState(false);
    let [desktopCatalogOpen, setDesktopCatalogOpen] = useState(false);

    const closeMenu = () => {
        setMenuOpen(false);
    };
    const openMenu = () => {
        setMenuOpen(true);
    };
    const closeDesktopMenu = () => {
        setDesktopCatalogOpen(false);
    };
    const openDesktopMenu = () => {
        setDesktopCatalogOpen(true);
    };
    const openCatalog = () => {
        setCatalogOpen(true);
    };
    const closeCatalog = () => {
        setCatalogOpen(false);
    };
    const trackTransition = (e) => {
        if (e.target.tagName === "A") {
            closeMenu();
            closeDesktopMenu();
        }
    };

    return (
        <>
            {menuOpen ? (
                <div className={classes.mobileMenu} onClick={closeMenu}>
                    <div className={classes.menu} onClick={(e) => e.stopPropagation()}>
                        <div className={classes.header}>
                            <div className={classes.avatar}>
                                <img src="./source/img/avatarPlug.png" alt="" />
                            </div>
                            <div className={classes.username}> </div>
                        </div>
                        <div className={classes.body}>
                            <div className={classes.linkSection} onClick={trackTransition}>
                                <CustomLink to={ROUTES.HOME}></CustomLink>
                                <div onClick={openCatalog}> </div>
                                <CustomLink to={ROUTES.ABOUT}> </CustomLink>
                                <CustomLink to={ROUTES.DELIVERY}>  </CustomLink>
                                <CustomLink to={ROUTES.CONTACTS}></CustomLink>
                                <CustomLink to={ROUTES.WARRANTY}></CustomLink>
                                {/* <CustomLink to={ROUTES.PRICE}>-</CustomLink> */}
                                <CustomLink to={ROUTES.BUYER}> </CustomLink>
                                <CustomLink to={ROUTES.FEEDBACK}></CustomLink>
                            </div>
                        </div>
                        <div className={classes.footer}>
                            <img className={classes.logo} src="./source/img/logo.svg" alt="Logo" />
                        </div>
                        {catalogOpen ? (
                            <div className={classes.catalogWrap}>
                                <div className={classes.searchWrap}>
                                    <Search placeholder="  " />
                                </div>
                                <div className={classes.backBtn} onClick={closeCatalog}>
                                    <img src="./source/img/icon/mobile/backArrow.svg" alt="backArrow" />
                                    
                                </div>

                                <div className={classes.catalog} onClick={trackTransition}>
                                    {categoryStore.category.map((cat) => {
                                        return cat.children?.length > 0 ? (
                                            <Accordion
                                                key={cat.id}
                                                title={cat.name}
                                                children={cat.children}
                                                path={ROUTES.CATALOG}
                                            />
                                        ) : (
                                            <Link to={ROUTES.CATALOG + cat.url} key={cat.id}>
                                                {cat.name}{" "}
                                            </Link>
                                        );
                                    })}
                                </div>
                                <div className={classes.footer}>
                                    <img className={classes.logo} src="./source/img/logo.svg" alt="Logo" />
                                </div>
                            </div>
                        ) : null}
                    </div>
                </div>
            ) : null}

            <div className={classes.mobileNavBar}>
                <div className={classes.item}>
                    <CustomLink to={ROUTES.HOME}>
                        <svg width="24" height="25" viewBox="0 0 24 25" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path
                                d="M13.2125 1.2375C12.8738 0.950318 12.4441 0.792694 12 0.792694C11.5559 0.792694 11.1262 0.950318 10.7875 1.2375L1.4125 9.19125C1.20491 9.36724 1.03811 9.58632 0.92371 9.83326C0.809305 10.0802 0.750029 10.3491 0.75 10.6212V22.375C0.75 22.8723 0.947544 23.3492 1.29917 23.7008C1.65081 24.0525 2.12772 24.25 2.625 24.25H8.5625C8.81114 24.25 9.0496 24.1512 9.22541 23.9754C9.40123 23.7996 9.5 23.5611 9.5 23.3125V15.5H14.5V23.3125C14.5 23.83 14.92 24.25 15.4375 24.25H21.375C21.8723 24.25 22.3492 24.0525 22.7008 23.7008C23.0525 23.3492 23.25 22.8723 23.25 22.375V10.6212C23.25 10.3491 23.1907 10.0802 23.0763 9.83326C22.9619 9.58632 22.7951 9.36724 22.5875 9.19125L13.2125 1.2375Z"
                                fill="#9099AD"
                            />
                        </svg>
                    </CustomLink>
                </div>
                <div className={classes.item + (basketStore.count ? " " + classes.active : "")}>
                    <CustomLink to={ROUTES.BASKET}>
                        <svg width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path
                                d="M26.25 4.99998H2.5V7.49998H5.375L9.475 18.75C9.73289 19.4794 10.2101 20.1112 10.8412 20.5588C11.4722 21.0064 12.2263 21.2478 13 21.25H23.75V18.75H13C12.7431 18.7499 12.4925 18.6706 12.2822 18.523C12.072 18.3754 11.9123 18.1666 11.825 17.925L11.25 16.25H22.85C23.3925 16.2492 23.9201 16.0719 24.353 15.745C24.786 15.418 25.1008 14.9591 25.25 14.4375L27.5 6.58748C27.5508 6.42333 27.5672 6.25047 27.5482 6.0797C27.5293 5.90892 27.4753 5.74389 27.3896 5.59492C27.304 5.44594 27.1886 5.31622 27.0506 5.21386C26.9125 5.1115 26.7549 5.03869 26.5875 4.99998C26.4757 4.98178 26.3618 4.98178 26.25 4.99998ZM22.8125 13.75H10.3125L8.0375 7.49998H24.5875L22.8125 13.75Z"
                                fill="#687082"
                            />
                            <path
                                d="M13.125 26.25C14.1605 26.25 15 25.4105 15 24.375C15 23.3395 14.1605 22.5 13.125 22.5C12.0895 22.5 11.25 23.3395 11.25 24.375C11.25 25.4105 12.0895 26.25 13.125 26.25Z"
                                fill="#687082"
                            />
                            <path
                                d="M20.625 26.25C21.6605 26.25 22.5 25.4105 22.5 24.375C22.5 23.3395 21.6605 22.5 20.625 22.5C19.5895 22.5 18.75 23.3395 18.75 24.375C18.75 25.4105 19.5895 26.25 20.625 26.25Z"
                                fill="#687082"
                            />
                        </svg>
                    </CustomLink>
                </div>
                <div className={classes.item + (favoriteStore.count ? " " + classes.active : "")}>
                    <CustomLink to={ROUTES.FAVORITE}>
                        <svg width="20" height="26" viewBox="0 0 20 26" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path
                                d="M17.5 0.5H2.5C1.12125 0.5 0 1.62125 0 3V25.5L10 19.785L20 25.5V3C20 1.62125 18.8787 0.5 17.5 0.5ZM17.5 21.1912L10 16.9062L2.5 21.1912V3H17.5V21.1912Z"
                                fill="#687082"
                            />
                        </svg>
                    </CustomLink>
                </div>
                <div className={classes.item}>
                    <svg width="24" height="25" viewBox="0 0 24 25" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path
                            d="M12 0.5C10.7639 0.5 9.55549 0.866556 8.52769 1.55331C7.49988 2.24007 6.6988 3.21619 6.22575 4.35823C5.75271 5.50027 5.62893 6.75693 5.87009 7.96931C6.11125 9.1817 6.7065 10.2953 7.58058 11.1694C8.45466 12.0435 9.5683 12.6388 10.7807 12.8799C11.9931 13.1211 13.2497 12.9973 14.3918 12.5242C15.5338 12.0512 16.5099 11.2501 17.1967 10.2223C17.8834 9.19451 18.25 7.98613 18.25 6.75C18.25 5.0924 17.5915 3.50269 16.4194 2.33058C15.2473 1.15848 13.6576 0.5 12 0.5ZM12 10.5C11.2583 10.5 10.5333 10.2801 9.91661 9.86801C9.29993 9.45596 8.81928 8.87029 8.53545 8.18506C8.25162 7.49984 8.17736 6.74584 8.32205 6.01841C8.46675 5.29098 8.8239 4.6228 9.34835 4.09835C9.8728 3.5739 10.541 3.21675 11.2684 3.07206C11.9958 2.92736 12.7498 3.00162 13.4351 3.28545C14.1203 3.56928 14.706 4.04993 15.118 4.66661C15.5301 5.2833 15.75 6.00832 15.75 6.75C15.75 7.74456 15.3549 8.69839 14.6517 9.40165C13.9484 10.1049 12.9946 10.5 12 10.5ZM23.25 24.25V23C23.25 20.6794 22.3281 18.4538 20.6872 16.8128C19.0462 15.1719 16.8206 14.25 14.5 14.25H9.5C7.17936 14.25 4.95376 15.1719 3.31282 16.8128C1.67187 18.4538 0.75 20.6794 0.75 23V24.25H3.25V23C3.25 21.3424 3.90848 19.7527 5.08058 18.5806C6.25269 17.4085 7.8424 16.75 9.5 16.75H14.5C16.1576 16.75 17.7473 17.4085 18.9194 18.5806C20.0915 19.7527 20.75 21.3424 20.75 23V24.25H23.25Z"
                            fill="#687082"
                        />
                    </svg>
                </div>
            </div>

            {desktopCatalogOpen && <div className={classes.closePlug} onClick={closeDesktopMenu}></div>}

            <header>
                <div className="container">
                    <div className={classes.mobileNavigationBar}>
                        <div onClick={openMenu}>
                            <img src="./source/img/icon/mobile/menu.svg" alt="menu" />
                        </div>
                        <div className={classes.logo}>
                            <Link to={ROUTES.HOME}>
                                <img src="./source/img/logo.svg" alt="Logo" />
                            </Link>
                        </div>
                        <div>
                            <img src="./source/img/icon/mobile/favourites.svg" alt="favourites" />
                        </div>
                    </div>
                    <div className={classes.navigationBar}>
                        <Link to="/">
                            <img className={classes.logo} src="./source/img/logo.svg" alt="Logo" />
                        </Link>
                        <div className={classes.navigation}>
                            <CustomLink to={"." + ROUTES.HOME}></CustomLink>
                            {/* <CustomLink to={ROUTES.PRODUCTS}> </CustomLink> */}
                            <CustomLink to={"." + ROUTES.ABOUT}> </CustomLink>
                            <CustomLink to={"." + ROUTES.DELIVERY}>  </CustomLink>
                            <CustomLink to={ROUTES.CONTACTS}></CustomLink>
                            <CustomLink to={ROUTES.WARRANTY}></CustomLink>
                            {/* <CustomLink to={ROUTES.PRICE}>-</CustomLink> */}
                            <CustomLink to={ROUTES.BUYER}> </CustomLink>
                            <CustomLink to={ROUTES.FEEDBACK}></CustomLink>
                        </div>
                    </div>
                    <div className={classes.desktopHeader}>
                        <div className={classes.catalogBtn} onClick={openDesktopMenu}>
                            <img src="./source/img/icon/menu.svg" alt="Logo" />
                             
                        </div>
                        <Search placeholder="iPhone S  " />
                        <div className={classes.navbar}>
                            <Link to={ROUTES.BASKET}>
                                <div className={classes.item}>
                                    <img src="./source/img/icon/basket.svg" alt="basket" />
                                    {!!basketStore.count && <div>{basketStore.count}</div>}
                                </div>
                            </Link>
                            <Link to={ROUTES.FAVORITE}>
                                <div className={classes.item}>
                                    <img src="./source/img/icon/favourites.svg" alt="favourites" />
                                    {!!favoriteStore.count && <div>{favoriteStore.count}</div>}
                                </div>
                            </Link>
                            <div className={classes.item}>
                                <img src="./source/img/icon/user.svg" alt="user" />
                            </div>
                        </div>
                    </div>
                </div>
                {desktopCatalogOpen && (
                    <div className={classes.desktopCatalog}>
                        <div className="container">
                            <div className={classes.catalog} onClick={trackTransition}>
                                {categoryStore.category.map((cat) => (
                                    <div>
                                        <div className={classes.aWrap}>
                                            <Link className={classes.parent} to={ROUTES.CATALOG + cat.url} key={cat.id}>
                                                {cat.name}
                                            </Link>
                                        </div>
                                        {cat.children?.map((subCat) => (
                                            <div className={classes.aWrap}>
                                                <Link to={ROUTES.CATALOG + subCat.url} key={subCat.id}>
                                                    {subCat.name}
                                                </Link>
                                            </div>
                                        ))}
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>
                )}
            </header>
        </>
    );
});
