import { useParams } from "react-router-dom";
import { BreadCrumps } from "components/UI/breadCrumps";
import classes from "./Catalog.module.css";
import { useStores } from "stores/root.store";
import { Product } from "components/UI/good/good";
import { Page404 } from "pages/404";
import { useEffect, useMemo, useState } from "react";
import { Pagination } from "components/UI/pagination";

export const Catalog = () => {
    let params = useParams();
    const [page, setPage] = useState(1);
    const { productsStore, categoryStore } = useStores();
    const category = categoryStore.getCategory({ url: params.category });
    useEffect(() => setPage(1), [category]);
    const products = useMemo(() => productsStore.getProducts(category.id), [category.id]);

    if (!category) return <Page404 />;

    const productPerPage = 20;
    const pages = Math.ceil(products.length / productPerPage);

    const paginationHandler = (page) => {
        setPage(page);
    };

    return (
        <div className="container">
            <div className={classes.breadCrumps}>
                <BreadCrumps pathArr={[{ name: category.longName }]} />
            </div>
            <div className={classes.catalog}>
                {products.length < 1 ? (
                    <h2 className={classes.empty}>  </h2>
                ) : (
                    <>
                        <h2 className={classes.empty}>
                            {category.longName} {products.length}
                        </h2>
                        <div className={classes.productWrap}>
                            {products
                                .slice(productPerPage * (page - 1), productPerPage * (page - 1) + productPerPage)
                                .map((product) => (
                                    <Product product={product} key={+product.id} />
                                ))}
                        </div>
                        <div className={classes.paginationBlock}>
                            <Pagination props={{ active: page, length: pages, onClick: paginationHandler }} />
                        </div>
                    </>
                )}
            </div>
        </div>
    );
};
