import classes from "./Popup.module.css";

export const Popup = ({ children, active, togglePopup }) => {
    const close = (e) => {
        if (~[classes.parent, classes.centerWindow].indexOf(e.target.className)) togglePopup();
    };

    return active ? (
        <div className={classes.parent} onClick={close}>
            <div className={classes.wrap}>
                <div className={classes.centerWindow}>{children}</div>
            </div>
        </div>
    ) : null;
};
